* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 100px;
  margin-top: 0 !important;
}

body {
  max-width: 100%;
}

.overflow {
  overflow: hidden;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  object-fit: cover;
}

.position-relative {
  position: relative;
}

.container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
  max-width: 1440px;
}

#wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

#main-wrapper {
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
}

.cover-bg {
  position: relative;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.responsive-media {
  display: inline-block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

iframe {
  max-width: 100%;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: normal;
  font-style: normal;
  color: #171719;
  font-size: 18px;
  line-height: 1.5;
  overflow: auto;
}
body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
body::-webkit-scrollbar-track {
  border-radius: 3px;
  background: #FFFFFF;
}
body::-webkit-scrollbar-thumb {
  transition: background-color 0.15s;
  border-radius: 3px;
  background: #171719;
}
body::-webkit-scrollbar-thumb:hover {
  background: rgba(23, 23, 25, 0.8);
}
body::-webkit-scrollbar-thumb:active {
  background: rgba(23, 23, 25, 0.9);
}

[class^=icon-]:before, [class*=" icon-"]:before {
  margin: 0;
}

strong, b, .font-weight-bold {
  font-weight: bold;
}

p {
  margin: 0 0 1em;
}

a {
  text-decoration: none;
  color: inherit;
}

.color-white {
  color: #FFFFFF;
}

.color-dark {
  color: #171719;
}

.bg-white {
  background-color: #FFFFFF;
}

.bg-dark {
  background-color: #171719;
}

.btn {
  font-size: 22px;
  font-weight: 500;
  line-height: 1.3;
  display: inline-block;
  align-items: center;
  border-radius: 28px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  user-select: none;
  transition-property: color, background-color, border-color;
  transition-timing-function: ease-in-out;
  transition-duration: 0.15s;
  padding: 14px 30px;
  white-space: nowrap;
  border-width: 1px;
  border-style: solid;
  color: initial;
}
.btn-primary {
  border-color: #171719;
  background-color: #171719;
  color: #FFFFFF;
}
.btn-primary:hover {
  background-color: #FFFFFF;
  color: #171719;
}
.btn-secondary {
  border-color: #171719;
}
.btn-secondary:hover {
  color: #FFFFFF;
  background-color: #171719;
}
.btn-secondary_light {
  border-color: #FFFFFF;
  color: #FFFFFF;
}
.btn-secondary_light:hover {
  color: #171719;
  background-color: #FFFFFF;
}